import React from 'react';
import { withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

var rows = [];

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);




const useStyles = makeStyles({
  table: {
    minWidth: 170,
    width: "100%",
 
  },

  TableContainer:{
    marginBottom: 50,
      
    
  },


  headTable: {
   display: 'flex',
   width: "100%",

  },

  rowTable: {
    width: "100%",
    display: 'flex',

  },

  headerTable: {
     display: 'flex',
     flex: 1,
     width: "100%",
     height: "auto",
     alignItems: "center" ,
     backgroundColor: '#fff',
     borderWidth: 0
  },

  cell: {
    borderWidth: 0,
    height: 'auto'
  }


});

export default function TecnicTable(props) {
  const classes = useStyles();

  

  return (
    <TableContainer className = {classes.TableContainer} component={Paper}>
      <Table className={classes.table} aria-label="customized table">
        <TableHead className={classes.headTable} >
          <TableRow className={classes.rowTable} >
            <StyledTableCell className={classes.headerTable} >
             <b style = {{fontSize: 20, color: "rgba(0,0,0,0.7)"}} > Ficha Técnica </b>
            </StyledTableCell>
          </TableRow>
        </TableHead>
        <TableBody >
          {props.tableData.map((row, index) => (
            <StyledTableRow key={index}>
              <StyledTableCell className = {classes.cell} component="th" scope="row">
                {row[0]} 
              </StyledTableCell>
              <StyledTableCell className = {classes.cell} align="left">{row[1]}</StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}